//=require components/1.js

$('.burger').on('click', function () {
	$('.burger__item').each(function (index) {
		$(this).toggleClass('active-item_' + (index + 1));
	});

	$('.header').toggleClass('header_active');
	$('.header-nav').toggleClass('header-nav_active');
});

$(function(){
    
    let $links = $('.header-nav__item a');
    let $footLinks = $('.footer-nav__item a');
    let $window = $(window);
    let $btnUp = $('.btnUp');
    let btnShowed = false;
    
    $links.on('click', function(e){
        e.preventDefault();

        $links.removeClass('active');
        let $link = $(this);//.addClass('active');
        let id = $link.attr('href');
        let $target = $(id);

        let start = $window.scrollTop();
        let dest = $target.offset().top - 70;
        let s = Math.abs(start - dest);

        if($target.length > 0){
            $('html,body').animate({
                scrollTop: dest
            }, s / 2);
        }
    });

    $footLinks.on('click', function(e){
        e.preventDefault();

        let $link = $(this);//.addClass('active');
        let id = $link.attr('href');
        let $target = $(id);

        let start = $window.scrollTop();
        let dest = $target.offset().top - 70;
        let s = Math.abs(start - dest);

        if($target.length > 0){
            $('html,body').animate({
                scrollTop: dest
            }, s / 2);
        }
    });

    $btnUp.on('click', function(){
        // s = t * v
        let s = $window.scrollTop();
        let pps = 3;

        $('html,body').animate({
            scrollTop: 0
        }, s / pps);
    });

    $window.on('scroll', onScroll);
    onScroll();

    function onScroll(){
        let pos = $window.scrollTop();

        if(!btnShowed && pos > 300){
            btnShowed = true;
            $btnUp.stop(true).fadeIn(500);
            //console.log('open');    
        }
        else if(btnShowed && pos <= 300){
            btnShowed = false;
            $btnUp.stop(true).fadeOut(500);
            //console.log('close');
        }

        $links.removeClass('active');
        let linkFound = false;

        for(let i = $links.length - 1; i >= 0; i--){
            let $link = $links.eq(i);
            let id = $link.attr('href');
            let $target = $(id);

            if(pos > $target.offset().top - 300){
                $link.addClass('active');
                linkFound = true;
                break;
            }
        }

        if(!linkFound){
            $links.eq(0).addClass('active');
        }
    }
});

